import React, { useEffect } from "react";

const CharLine = () => {
  useEffect(() => {
    const data = [
      {
        name: "USA",
        values: [
          { date: "2000", price: "100" },
          { date: "2001", price: "110" },
          { date: "2002", price: "145" },
          { date: "2003", price: "241" },
          { date: "2004", price: "101" },
          { date: "2005", price: "90" },
          { date: "2006", price: "10" },
          { date: "2007", price: "35" },
          { date: "2008", price: "21" },
          { date: "2009", price: "201" },
        ],
      },
      {
        name: "Canada",
        values: [
          { date: "2000", price: "200" },
          { date: "2001", price: "120" },
          { date: "2002", price: "33" },
          { date: "2003", price: "21" },
          { date: "2004", price: "51" },
          { date: "2005", price: "190" },
          { date: "2006", price: "120" },
          { date: "2007", price: "85" },
          { date: "2008", price: "221" },
          { date: "2009", price: "101" },
        ],
      },
      {
        name: "Maxico",
        values: [
          { date: "2000", price: "50" },
          { date: "2001", price: "10" },
          { date: "2002", price: "5" },
          { date: "2003", price: "71" },
          { date: "2004", price: "20" },
          { date: "2005", price: "9" },
          { date: "2006", price: "220" },
          { date: "2007", price: "235" },
          { date: "2008", price: "61" },
          { date: "2009", price: "10" },
        ],
      },
    ];

    const width = 750;
    const height = 300;
    const margin = 50;
    const duration = 250;

    const lineOpacity = "0.25";
    const lineOpacityHover = "0.85";
    const otherLinesOpacityHover = "0.1";
    const lineStroke = "1.5px";
    const lineStrokeHover = "2.5px";

    const circleOpacity = "0.85";
    const circleOpacityOnLineHover = "0.25";
    const circleRadius = 3;
    const circleRadiusHover = 6;

    /* Format Data */

    const parseDate = d3.timeParse("%Y");
    data.forEach(function (d) {
      // eslint-disable-next-line @typescript-eslint/no-shadow
      d.values.forEach(function (d) {
        d.date = parseDate(d.date);
        d.price = +d.price;
      });
    });

    /* Scale */
    const xScale = d3
      .scaleTime()
      .domain(d3.extent(data[0].values, (d) => d.date))
      .range([0, width - margin]);

    const yScale = d3
      .scaleLinear()
      .domain([0, d3.max(data[0].values, (d) => d.price)])
      .range([height - margin, 0]);

    const color = d3.scaleOrdinal(d3.schemeCategory10);

    /* Add SVG */
    const svg = d3
      .select("#chart")
      .append("svg")
      .attr("width", `${width + margin}px`)
      .attr("height", `${height + margin}px`)
      .attr("class", "svg")
      .append("g")
      .attr("transform", `translate(${margin}, ${margin})`);

    /* Add line into SVG */
    const line = d3
      .line()
      .x((d) => xScale(d.date))
      .y((d) => yScale(d.price));

    const lines = svg.append("g").attr("class", "lines");

    lines
      .selectAll(".line-group")
      .data(data)
      .enter()
      .append("g")
      .attr("class", "line-group")
      // .on("mouseover", function (d, i) {
      //   svg
      //     .append("text")
      //     .attr("class", "title-text")
      //     .style("fill", color(i))
      //     .text(d.name)
      //     .attr("text-anchor", "middle")
      //     .attr("x", (width - margin) / 2)
      //     .attr("y", 5);
      // })
      // .on("mouseout", function (d) {
      //   svg.select(".title-text").remove();
      // })
      .append("path")
      .attr("class", "line")
      .attr("d", (d: any[]) => line(d.values))
      .style("stroke", (d: any, i: any) => color(i))
      .style("opacity", lineOpacity)
      .on("mouseover", function (d: any) {
        d3.selectAll(".line").style("opacity", otherLinesOpacityHover);
        d3.selectAll(".circle").style("opacity", circleOpacityOnLineHover);
        d3.select(this)
          .style("opacity", lineOpacityHover)
          .style("stroke-width", lineStrokeHover)
          .style("cursor", "pointer");
      })
      .on("mouseout", function (d) {
        d3.selectAll(".line").style("opacity", lineOpacity);
        d3.selectAll(".circle").style("opacity", circleOpacity);
        d3.select(this)
          .style("stroke-width", lineStroke)
          .style("cursor", "none");
      });

    /* Add circles in the line */
    // lines
    //   .selectAll("circle-group")
    //   .data(data)
    //   .enter()
    //   .append("g")
    //   .style("fill", (d, i) => color(i))
    //   .selectAll("circle")
    //   .data((d) => d.values)
    //   .enter()
    //   .append("g")
    //   .attr("class", "circle")
    //   .on("mouseover", function (d) {
    //     d3.select(this)
    //       .style("cursor", "pointer")
    //       .append("text")
    //       .attr("class", "text")
    //       .text(`${d.price}`)
    //       // eslint-disable-next-line @typescript-eslint/no-shadow
    //       .attr("x", (d) => xScale(d.date) + 5)
    //       // eslint-disable-next-line @typescript-eslint/no-shadow
    //       .attr("y", (d) => yScale(d.price) - 10);
    //   })
    //   .on("mouseout", function (d) {
    //     d3.select(this)
    //       .style("cursor", "none")
    //       .transition()
    //       .duration(duration)
    //       .selectAll(".text")
    //       .remove();
    //   })
    //   .append("circle")
    //   .attr("cx", (d) => xScale(d.date))
    //   .attr("cy", (d) => yScale(d.price))
    //   .attr("r", circleRadius)
    //   .style("opacity", circleOpacity)
    //   .on("mouseover", function (d) {
    //     d3.select(this)
    //       .transition()
    //       .duration(duration)
    //       .attr("r", circleRadiusHover);
    //   })
    //   .on("mouseout", function (d) {
    //     d3.select(this).transition().duration(duration).attr("r", circleRadius);
    //   });

    /* Add Axis into SVG */
    const xAxis = d3.axisBottom(xScale).ticks(5);
    const yAxis = d3.axisRight(yScale).ticks(5);
    svg
      .append("g")
      .attr("class", "xaxis")
      .attr("transform", `translate(0, ${height - margin})`)
      .call(xAxis);

    svg
      .append("g")
      .attr("class", "yaxis")
      .attr("transform", `translate(700 ,0)`)
      .call(yAxis)
      .append("text")
      .attr("y", 15)
      .attr("fill", "#000");

    svg
      .append("g")
      .attr("class", "ticks")
      .attr("transform", `translate(0, 0)`)
      .attr("stroke-width", 0.1)
      .attr("fill", "#282A30")
      .call(d3.axisLeft(yScale).tickSize(-width).tickFormat(""));
  }, []);

  return <div id="chart" />;
};

export default CharLine;
