import React from "react";
import { Slider } from "@material-ui/core";
import styles from "../../pages/main/styles.module.scss";

type Props = {
  sliderData: number;
  setSliderData: (value: string) => void;
};

const InputSlider: React.FC<Props> = ({ sliderData, setSliderData }: Props) => {
  const onChange = (event: any, newValue: any) => {
    setSliderData(newValue);
  };
  return (
    <>
      <input
        value={sliderData}
        onChange={(e) => setSliderData(e.target.value)}
        type="text"
        className={styles.input}
      />
      <Slider value={sliderData} onChange={onChange} />
    </>
  );
};

export default InputSlider;
