import React from "react";
import classNames from "classnames";
import styles from "./styles.module.scss";

type Props = {
  text?: string;
  url?: string;
  classes?: string;
};

const LinkArrow: React.FC<Props> = ({ text, url = "/#", classes }: Props) => {
  return (
    <a className={classNames(styles.link, classes)} href={url}>
      <p>{text}</p>
    </a>
  );
};

export default LinkArrow;
